"use client";
import { useMemo } from 'react';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend, ReferenceLine, ReferenceArea } from 'recharts';
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameDay, startOfYear, endOfYear, eachMonthOfInterval, isSameMonth } from 'date-fns';
import { es } from 'date-fns/locale';

export default function ComplexFinanceChart({ transactions, period, currentDate }: any) {
    
    // 1. Calculamos la "Etiqueta" del día seleccionado para saber dónde poner la línea
    const selectedLabel = useMemo(() => {
        if (!currentDate) return null;
        
        // Debe coincidir EXACTAMENTE con el formato de abajo
        if (period === 'year') {
            const label = format(currentDate, 'MMM', { locale: es });
            return label.charAt(0).toUpperCase() + label.slice(1);
        } else {
            return format(currentDate, 'd', { locale: es });
        }
    }, [currentDate, period]);

    const data = useMemo(() => {
        let intervalPoints: Date[] = [];
        let dateFormat = 'd';

        if (period === 'year') {
             intervalPoints = eachMonthOfInterval({ start: startOfYear(currentDate), end: endOfYear(currentDate) });
             dateFormat = 'MMM';
        } else {
             intervalPoints = eachDayOfInterval({ start: startOfMonth(currentDate), end: endOfMonth(currentDate) });
             dateFormat = 'd';
        }

        let cumulative = 0;

        return intervalPoints.map(point => {
            const pointTx = transactions.filter((t: any) => {
                const tDate = new Date(t.date);
                if (period === 'year') return isSameMonth(tDate, point);
                return isSameDay(tDate, point);
            });

            const income = pointTx.filter((t: any) => t.type === 'income').reduce((sum: number, t: any) => sum + t.amount, 0);
            const expense = pointTx.filter((t: any) => t.type === 'expense' && !t.isSavings).reduce((sum: number, t: any) => sum + t.amount, 0);
            const savings = pointTx.filter((t: any) => t.isSavings).reduce((sum: number, t: any) => sum + t.amount, 0);
            const netFlow = income - expense - savings; 
            
            cumulative += netFlow; 

            let labelX = format(point, dateFormat, { locale: es });
            if (period === 'year') {
                labelX = labelX.charAt(0).toUpperCase() + labelX.slice(1);
            }

            return {
                label: labelX,
                fullDate: format(point, 'PPP', { locale: es }),
                Ingreso: income,
                Gasto: expense,
                Ahorro: savings,
                Restante: cumulative
            };
        });
    }, [transactions, period, currentDate]);

    const formatMoneyAxis = (val: number) => {
        if (Math.abs(val) >= 1000000) return `$${(val / 1000000).toFixed(1)}M`;
        if (Math.abs(val) >= 1000) return `$${(val / 1000).toFixed(0)}k`;
        return `$${val}`;
    };

    return (
        <div className="h-[350px] w-full relative">
            <ResponsiveContainer width="100%" height="100%">
                <AreaChart data={data} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
                    <defs>
                        <linearGradient id="colorIng" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#10b981" stopOpacity={0.2}/><stop offset="95%" stopColor="#10b981" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorGas" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#f43f5e" stopOpacity={0.2}/><stop offset="95%" stopColor="#f43f5e" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorSav" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.2}/><stop offset="95%" stopColor="#3b82f6" stopOpacity={0}/>
                        </linearGradient>
                        <linearGradient id="colorRes" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#a855f7" stopOpacity={0.1}/><stop offset="95%" stopColor="#a855f7" stopOpacity={0}/>
                        </linearGradient>
                        {/* Nuevo degradado para el resaltado */}
                        <linearGradient id="highlightGradient" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="0%" stopColor="#fbbf24" stopOpacity={0.1}/>
                            <stop offset="100%" stopColor="#fbbf24" stopOpacity={0.0}/>
                        </linearGradient>
                    </defs>
                    
                    <CartesianGrid strokeDasharray="3 3" stroke="#27272a" vertical={false} />
                    
                    <XAxis 
                        dataKey="label" 
                        stroke="#52525b" 
                        tickLine={false} 
                        axisLine={false} 
                        interval={period === 'year' ? 0 : 'preserveStartEnd'} 
                        minTickGap={10}
                    />
                    
                    <YAxis 
                        stroke="#52525b" 
                        tickLine={false} 
                        axisLine={false} 
                        width={60}
                        tickFormatter={formatMoneyAxis} 
                        allowDataOverflow={false}
                    />
                    
                    <Tooltip 
                        contentStyle={{ backgroundColor: '#09090b', borderColor: '#27272a', borderRadius: '8px' }}
                        formatter={(val: number) => new Intl.NumberFormat('es-CO', { style: 'currency', currency: 'COP', maximumFractionDigits: 0 }).format(val)}
                        labelStyle={{ color: '#a1a1aa', marginBottom: '0.5rem' }}
                    />
                    
                    {/* --- AQUÍ ESTÁ EL RESALTADO --- */}
                    {selectedLabel && (
                        <ReferenceLine 
                            x={selectedLabel} 
                            stroke="#fbbf24" // Color Ámbar/Amarillo
                            strokeDasharray="3 3" 
                            strokeOpacity={0.8}
                        />
                    )}
                    {/* Opción extra: Un área sombreada detrás si quisieras (comentada por ahora para no saturar) */}
                    {/* <ReferenceArea x1={selectedLabel} x2={selectedLabel} fill="url(#highlightGradient)" /> */}

                    <Legend wrapperStyle={{ paddingTop: '10px' }} />
                    
                    <Area type="monotone" dataKey="Ingreso" stroke="#10b981" fill="url(#colorIng)" strokeWidth={2} />
                    <Area type="monotone" dataKey="Ahorro" stroke="#3b82f6" fill="url(#colorSav)" strokeWidth={2} />
                    <Area type="monotone" dataKey="Gasto" stroke="#f43f5e" fill="url(#colorGas)" strokeWidth={2} />
                    
                    <Area 
                        type="monotone" 
                        dataKey="Restante" 
                        stroke="#a855f7" 
                        fill="url(#colorRes)" 
                        strokeWidth={3} 
                        strokeDasharray="4 4" 
                    />
                </AreaChart>
            </ResponsiveContainer>
            
            {/* Etiqueta flotante opcional sobre la gráfica para indicar qué se ve */}
            <div className="absolute top-2 right-2 bg-zinc-900/80 border border-zinc-700 px-2 py-1 rounded text-[10px] text-zinc-400 pointer-events-none">
                {period === 'year' ? 'Vista Anual' : 'Vista Mensual'}
            </div>
        </div>
    );
}